*{
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
}
.center{
  max-width: 960px;
  margin:0 auto;
  padding:0 2% ;
  display: flex;
  flex-wrap: wrap;
}
.main{
  padding: 40px 0;
  width: 100%;
  background-color: blueviolet;
  min-height: 400px;
  background-position: center;
  background-size: cover;
  background-image: url('https://cursos.dankicode.com/app/Views/public/images/home/banner-home.png');
}
.menu{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
 
}
.menu h3{
  color:white;

}
.menu a{
  color: white;
  text-decoration: none;
  border: 1px solid;
  padding: 3px 8px;
  border-radius: 5px;
}
.form{

  display: flex;
  width: 100% ;
  max-width: 600px;
  margin: 40px auto;
  flex-wrap: wrap;
  justify-content: center;
}
.form h2{
  color:white;
  text-transform: uppercase;
  font-weight: normal;
  font-size: 28px;
}


.form input[type=text]{
  width: 30%;
  justify-content: space-between;
  height: 30px;
  margin: 8px 0px ;
 


}
.itens-form{
  margin: 0 auto;
  max-width: 520px;
  display: flex;
  width: 100%;
  justify-content: space-around;

  flex-wrap: wrap;
}

.itens-form input[type=text]{
  width: 48%;
  height: 30px;
  margin:8px 0px;
  padding-left: 10px;
}
.itens-form input[type=submit]{
  width: 100%;
  height: 40px;
  background-color: #e7426b;
  cursor: pointer;
  color: white;
  border:0;
  font-size: 19px;
}
.conteudo-single{
  margin-top: 40px;
  width: 50%;
  padding: 8px 30px;
}
@media screen and (max-width: 768px) {
  .itens-form input[type=text] {
    width: 100%;}
    .conteudo-single{width: 100%;}
}
